import { default as allExercises } from '../assets/exercises';
import _ from 'lodash';

export default (state = {}, action) => {
  const { mode } = action;
  switch (action.type) {
    // case 'ADD_EXERCISE':
    //   return {
    //     ...state,
    //     exercises: exercises.concat(action.payload)
    //   }
    case 'GET_EXERCISES': {
      const { [mode]: exercises = [] } = allExercises;
      const { [mode]: { latestPassword } = {}} = state;
      console.log(exercises, latestPassword);
      const latestIndex = exercises.findIndex(e => e.password === latestPassword);
      if (latestIndex === -1) {
        return [];
      }
      return exercises.slice(0, latestIndex);
    }
    case 'UNLOCK_EXERCISE': {
      const {password = ''} = action;
      const { [mode]: exercises = [] } = allExercises;
      console.log('all', exercises, mode, allExercises);
      const newExerciseIndex = _.findIndex(exercises, ({password: pw}) => !!pw && pw.toUpperCase() === password.toUpperCase());
      if (newExerciseIndex !== -1) {
        const newExercises = exercises.slice(newExerciseIndex);
        if (!state[mode] || newExercises.length > state[mode].unlockedExercisesLength) {
          return {
            ...state,
            [mode]: {
              unlockedExercisesLength: newExercises.length,
              latestPassword: password
            }
          }
        }
      }
      return state;
    }
      
   default:
    return state
  }
 }