import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { ExpansionPanel, ExpansionPanelDetails, ExpansionPanelSummary, Typography } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const styles = theme => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  }
});

class _Mission extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      expanded: false
    }
  }
  toggleExpanded() {
    this.setState({
      expanded: !this.state.expanded
    });
  }
  indexToMissionName(index) {
    const letter = String.fromCharCode(97 + index).toUpperCase();
    return <strong>Mission {letter}</strong>;
  }
  render() {
    const { description, points, exercise, index, classes } = this.props;
    const { expanded } = this.state;
    return (
      <ExpansionPanel expanded={expanded} onChange={() => this.toggleExpanded()}>
          <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
            <Typography className={classes.heading}>{this.indexToMissionName(index)} ({points} Nectar Points)</Typography>
            <Typography className={classes.secondaryHeading}>{description}</Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <Typography>
            {exercise}
            </Typography>
          </ExpansionPanelDetails>
        </ExpansionPanel>
    );
  }
}
const Mission = withStyles(styles)(_Mission);

class _Missions extends React.Component {
  render() {
    const { missions, classes } = this.props;
    return (
      <div className={classes.root}>
        {missions.map(({description, points, exercise}, index) => <Mission key={description} index={index} description={description} points={points} exercise={exercise} />)}
      </div>
    )
  }
}

const Missions = withStyles(styles)(_Missions);

class ViewExercise extends React.Component {
  render() {
    const { exercise: { title, missions } } = this.props;
    return (
      <>
      <Typography variant="subtitle1">You must complete AT LEAST ONE of the following missions to summon Hermes to bring news of the next missions.</Typography>
      <Missions title={title} missions={missions} />
      </>
    );
    
  }
}

export default ViewExercise;