export const getExercises = (mode) => dispatch => {
  dispatch({
    type: 'GET_EXERCISES',
    mode
  });
}

export const unlockExercise = (mode, password) => dispatch => {
  dispatch({
    type: 'UNLOCK_EXERCISE',
    mode,
    password
  });
}